<style></style>

<script>
import { ref } from "vue";

const path = "protocolosmedicamentos";
const setup = {
  url: `https://wrochadesign.com.br/clientes/invet/?t=${path}`,
  urlId: `https://wrochadesign.com.br/clientes/invet/?t=${path}&id=`,
};

const columns = [
  //{ name: "id", label: "id", field: "id" },
  {
    name: "medicamentos",
    label: "Procedimentos",
    field: "medicamentos",
    sortable: true,
    required: true,
  },
  { name: "vol", label: "vol. Ml ", field: "vol" },
  { name: "via", label: "Via", field: "via" },
  { name: "freq", label: "Freq.", field: "freq" },
  { name: "h06", label: "06h", field: "h06" },
  { name: "h07", label: "07h", field: "h07" },
  { name: "h08", label: "08h", field: "h08" },
  { name: "h09", label: "09h", field: "h09" },
  { name: "h10", label: "10h", field: "h10" },
  { name: "h11", label: "11h", field: "h11" },
  { name: "h12", label: "12h", field: "h12" },
  { name: "h13", label: "13h", field: "h13" },
  { name: "h14", label: "14h", field: "h14" },
  { name: "h15", label: "15h", field: "h15" },
  { name: "h16", label: "16h", field: "h16" },
  { name: "h17", label: "17h", field: "h17" },
  { name: "h18", label: "18h", field: "h18" },
  { name: "h19", label: "19h", field: "h19" },
  { name: "h20", label: "20h", field: "h20" },
  { name: "h21", label: "21h", field: "h21" },
  { name: "h22", label: "22h", field: "h22" },
  { name: "h23", label: "23h", field: "h23" },
  { name: "h24", label: "24h", field: "h24" },
  { name: "h01", label: "01h", field: "h01" },
  { name: "h02", label: "02h", field: "h02" },
  { name: "h03", label: "03h", field: "h03" },
  { name: "h04", label: "04h", field: "h04" },
  { name: "h05", label: "05h", field: "h05" },

  
];

const item = [
  {
    id: 0,
    //idpet: 1,
    medicamentos: "",
    vol: "0",
    via: "0",
    freq: "0",
    h06: "0",
    h07: "0",
    h08: "0",
    h09: "0",
    h10: "0",
    h11: "0",
    h12: "0",
    h13: "0",
    h14: "0",
    h15: "0",
    h16: "0",
    h17: "0",
    h18: "0",
    h19: "0",
    h20: "0",
    h21: "0",
    h22: "0",
    h23: "0",
    h24: "0",
    h01: "0",
    h02: "0",
    h03: "0",
    h04: "0",
    h05: "0",
    status: 1,
    dataTime: ""
  },
];

export default {
  name: "TabMedicamentos",
  components: {},
  props: {
    card: Array,
  },
  setup() {
    const rows = ref([]);
    const rowCount = ref(10);
    const loading = ref(false);

    rows.value.reverse();

    return {
      color: "red",
      setup: setup,
      selected: ref([]),
      filter: ref(""),
      leftDrawerOpen: ref(false),
      rows,
      item: ref(item),
      rowCount,
      columns,
      loading,
      async addRow() {
        const index = rows.value.length + 1;

        const row = item[0];
        row.id = index;

        const newRow = { ...row }; // extend({}, row, { name: `${row.name} (${row.__count})` })

        rows.value = [
          ...rows.value.slice(0, index),
          newRow,
          ...rows.value.slice(index),
        ];

        rows.value.reverse();

        const id = newRow.id;
        console.log(id);

        const dataJson = JSON.stringify(newRow);

        const req = await fetch(this.setup.url, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: dataJson,
        });

        const res = await req.json();
        console.log(res);
      },
    };
  },
  data() {
    return {
      title: "Alimentao",
      desc: "Lorem ipsum dolor sit amet consectetur adipisicing elit.", 
    };
  },
  methods: {
    labelDeletar() {
      return "Clique no boto deletar para excluir esse item.";
    },
    async getServicos() {
      const req = await fetch(this.setup.url);
      this.rows = await req.json();
      console.log(this.rows);
    },
    async setApi(dataJson) {
      const req = await fetch(this.setup.url, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: dataJson,
      });

      const res = await req.json();
      console.log(res);
    },

    async updateApi(id, key, value, scope) {
      const array = [];
      array[key] = value;
      console.log(array);

      const dataJson = JSON.stringify(Object.assign({}, array));

      const req = await fetch(this.setup.urlId + id, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: dataJson,
      });
      const res = await req.json();
      console.log(res);
      scope.set();
    },
    async deleteApi(array) {
      const id = array[0].id;
      console.log(id);

      const dataJson = JSON.stringify({ status: 0 });

      const req = await fetch(this.setup.urlId + id, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: dataJson,
      });

      const res = await req.json();
      this.selected = "";
      console.log(res);
      this.getServicos();
    },
  },
  mounted() {
    this.getServicos();
  },
};
    
</script>