<script>
import { useQuasar } from "quasar";
import { ref } from "vue";
import Header from "../../components/Header.vue";
import FichaTable from "../../components/Pacientes/TabFichaTable.vue";
import TabBoletim from "../../components/Pacientes/TabBoletim.vue";
import TabProtocolos from "../../components/Pacientes/TabProtocolos.vue";
import TabExames from "../../components/Pacientes/TabExames.vue";
import TabImagens from "../../components/Pacientes/TabImagens.vue";
import TabFinanceiro from "../../components/Pacientes/TabFinanceiro.vue";

const today = new Date();
const today2 = today.setDate(today.getDate()+1);
console.log(new Date(today2));

const user = localStorage.getItem('user') ?? null

//const path = "tablehour";
const setup = {
  origin: process.env.VUE_APP_URL_API_ORIGIN,
  url: process.env.VUE_APP_URL_API_CARDS,
};

export default {
  name: "Home",
  components: {
    Header,
    FichaTable,
    TabBoletim,
    TabProtocolos,
    TabExames,
    TabImagens,
    TabFinanceiro,
  },

  setup() {
    const card = ref({});
    const client = ref({});
    const pet = ref({});
    const loading = ref(false);
    const $q = useQuasar();

    return {
      dd: today.getDate(),
      mm: today.getMonth()+1,
      yyyy: today.getFullYear(),
      today: today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate(),

      day: ref(today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate()),
      days: ref([]),
      tab: ref("ficha"),
      setup: setup,
      collaborator: user,
      leftDrawerOpen: ref(false),
      grid:  ref([]),
      card,
      client,
      pet,
      loading,
      showNotif(msg, color, icon) {
        $q.notify({
          message: msg,
          color: color,
          icon: icon,
        });
      },
    };
  },
  data() {
    return {
      pageTitle: "Ficha",
      pageDesc: "teste",
    };
  },
  methods: {
    async gridDate(){


      
      const date1 = new Date();
      const date2 = new Date(this.card.entry);
      const diffTime = Math.abs(date1 - date2);
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));

      console.log("diffDays",diffDays)
      
      for(let i = 0; i < diffDays+1; i++){


        const fullDay = new Date(date2.getTime() + (1000 * 60 * 60 * 24*i));       
        
        let valueDay = fullDay.toISOString().slice(0,10)

        let arrayValueDay = valueDay.split('-')

        let labelDay = i === (diffDays) ? 'Hoje' : arrayValueDay[2] +'/'+arrayValueDay[1]

        if(labelDay === 'Hoje'){
          this.day = valueDay
        }

        this.grid.push({label: labelDay, value: valueDay}) 

      }
      this.grid.reverse()
      console.log(diffTime + " milliseconds");
      console.log(diffDays + " days");
      const grid = [


      ]
      console.log(grid) 
             
    },
    async getApi() {
      const req = await fetch(this.setup.url + "/" + this.$route.params.id + "/" + this.day);
      //const req = await fetch('https://clientes.test/invet/api/cards/4');
      //const req = await fetch('https://agpublick.com.br/invet/api/cards/');
      this.data = await req.json();

      console.log("api url:", this.setup.url);

      if (this.data.status === "error") {
        this.showNotif(
          "Nenhum registro encontrado!",
          "red",
          "las la-thumbs-up"
        );
        this.$router.go(-1);
        return false;
      } else {
        this.card = this.data.data.card;
        this.pet = this.data.data.pet;
        this.client = this.data.data.client;
        this.gridDate()
      }
    },
  },
  created() {  
   
    this.getApi()
  },
  mounted() {
    //this.getApi();
    
  },
  computed:{
    labelType(){
      let type = ''
      switch(this.pet.type){
        case 'canine' : type = 'Canino'
        break
        case 'feline' : type = 'Felino'
        break
      }
      return type
    },
    labelGender(){
      let gender = ''
      switch(this.pet.gender){
        case 'male' : gender = 'Macho'
        break
        case 'female' : gender = 'Fmea'
        break
      }
      return gender
    },
    labelBirth(){
      let options = { year: 'numeric', month: 'long', day: 'numeric' }
      let date =  new Date(this.pet.date_of_birth).toLocaleDateString("pt-BR", options)
      return date
    }
    },
};
</script>
