                || (petType==='feline' && (props.row.pt < 5.4 || props.row.pt > 7.8) ? 'text-orange':'text-primary')
              "
            >
              {{ props.row.pt }}
              <q-popup-edit
                class="bg-grey-2"
                v-model.number="props.row.pt"
                v-slot="scope"
                label-set="Salvar"
                label-cancel="Cancelar"
              >
                <q-input
                  type="number"
                  v-model.number="scope.value"
                  dense
                  autofocus
                  @keyup.enter="
                    updateApi(props.row.id, 'pt', scope.value, scope)
                  "
                >
                  <template v-slot:append>
                    <q-icon name="las la-pen" />
                  </template>
                  <template v-slot:after>
                    <q-btn
                      flat
                      dense
                      color="negative"
                      icon="cancel"
                      @click.stop="scope.cancel"
                    />

                    <q-btn
                      flat
                      dense
                      color="positive"
                      icon="check_circle"
                      @click.stop="
                        updateApi(props.row.id, 'pt', scope.value, scope)
                      "
                      :disable="
                        scope.validate(scope.value) === false ||
                        scope.initialValue === scope.value
                      "
                    />
                  </template>
                </q-input>
              </q-popup-edit>
            </q-td>
            <!--end td pt-->
            <!--td conv-->
            <q-td key="conv" :props="props">
              {{ props.row.conv }}
              <q-popup-edit
                class="bg-grey-2"
                v-model.number="props.row.conv"
                v-slot="scope"
                label-set="Salvar"
                label-cancel="Cancelar"
              >
                <q-input
                  type="number"
                  v-model.number="scope.value"
                  dense
                  autofocus
                  @keyup.enter="
                    updateApi(props.row.id, 'conv', scope.value, scope)
                  "
                >
                  <template v-slot:append>
                    <q-icon name="las la-pen" />
                  </template>
                  <template v-slot:after>
                    <q-btn
                      flat
                      dense
                      color="negative"
                      icon="cancel"
                      @click.stop="scope.cancel"
                    />

                    <q-btn
                      flat
                      dense
                      color="positive"
                      icon="check_circle"
                      @click.stop="
                        updateApi(props.row.id, 'conv', scope.value, scope)
                      "
                      :disable="
                        scope.validate(scope.value) === false ||
                        scope.initialValue === scope.value
                      "
                    />
                  </template>
                </q-input>
              </q-popup-edit>
            </q-td>
            <!--end td conv-->
          </q-tr>
        </template>
      </q-table>
      <!--Table end-->
      <template>
        <div>
                {{today}}
      {{apiLoad}}
        </div>
      </template>

    </div>
  </div>
</template>



<script>

import { ref } from "vue";

const user = localStorage.getItem('user') ?? null

const setup = {
  origin: process.env.VUE_APP_URL_API_ORIGIN,
  url: process.env.VUE_APP_URL_API_CARDS_REGISTERS,
  title: "Ficha",
  description: "teste",
  noResultsLabel: "Nenhum resultado foi encontrado!",
  noDataLabel: "Desculpe. Algo inesperado ocorreu!",
  visible_columns: "['hour','mental','dc','resp','toc','hird','muc','sons_cardiacos','sons_pulmonares','fc','fr','pa','pulso','tpc','t2','pas','du','lact','sp02','so2','ph','hc03','pc02','tc02','gaso','bhb','glic','k','na','cl','ecg','ht','pt','conv']"
};

console.log(setup)

const columns = [
  {
    name: "hour",
    align: "left",
    label: "Hora",
    sortable: true,
    required: true,
    field: row => row.name,
    format: val => `${val}`,
  },
  { name: "mental", label: "Mental", field: "mental" }, //
  { name: "dc", label: "Dec", field: "dec" },
  { name: "resp", label: "resp", field: "resp" }, //select
  { name: "toc", label: "TC", field: "toc" }, ///temperatura escrita - 37,5
  { name: "hird", label: "Hird", field: "hird" }, //select
  { name: "muc", label: "Muc", field: "muc" }, //select ictrica 1, 2,3
  { name: "sons_cardiacos", label: "Sons Cardiacos", field: "sons_cardiacos" }, //select
  {
    name: "sons_pulmonares", //
    label: "Sons Pulmonares",
    field: "sons_pulmonares",
  },
  { name: "fc", label: "Fc", field: "fc" },
  { name: "fr", label: "Fr", field: "fr" },
  { name: "pa", label: "PA", field: "pa" }, //livre escrita (texto)
  { name: "pulso", label: "Pulso", field: "pulso" },
  { name: "tpc", label: "TPC", field: "tpc" },
  { name: "t2", label: "T2", field: "t2" },
  { name: "pas", label: "PAS", field: "pas" },
  { name: "du", label: "D.U.", field: "du" },
  { name: "lact", label: "Lact", field: "lact" },
  { name: "sp02", label: "spO2", field: "sp02" },
  { name: "po2", label: "po2", field: "po2" },
  { name: "so2", label: "so2", field: "so2" },
  { name: "ph", label: "pH", field: "ph" },
  { name: "hc03", label: "HCO3", field: "hc03" },
  { name: "pc02", label: "pCO2", field: "pc02" },
  { name: "tc02", label: "tCO2", field: "tc02" },
  { name: "gaso", label: "gaso", field: "gaso" },
  { name: "bhb", label: "bhb", field: "bhb" },
  { name: "glic", label: "glic", field: "glic" },
  { name: "k", label: "k", field: "k" },
  { name: "na", label: "na", field: "na" },
  { name: "cl", label: "cl", field: "cl" },
  { name: "ecg", label: "ecg", field: "ecg" },
  { name: "ht", label: "ht", field: "ht" },
  { name: "pt", label: "pt", field: "pt" },
  { name: "conv", label: "conv", field: "conv" },
  { name: "id", label: "#", field: "id" },
];

const item = [
  {
    id: null,
    caid: null,
    pid: null,
    hour: "0",
    mental: "0",
    dc: "0",
    resp: "0",
    toc: "0",
    hird: "0",
    muc: "0",
    sons_cardiacos: "0",
    sons_pulmonares: "0",
    fc: "0",
    fr: "0",
    pa: "0",
    pulso: "0",
    tpc: "0",
    t2: "0",
    pas: "0",
    du: "0",
    lact: "0",
    sp02: "0",
    po2: "0",
    so2: "0",
    ph: "0",
    hco3: "0",
    pco2: "0",
    tco2: "0",
    gaso: "0",
    bhb: "0",
    glic: "0",
    k: "0",
    na: "0",
    cl: "0",
    ecg: "0",
    ht: "0",
    pt: "0",
    conv: "0",
    status: 'active',
  },
];

export default {
  name: "TableFicha",
  props: {
    card: Object,
    petType: String,
    today: String
  },
  setup() {
    const newregister = ref([]);
    const rows = ref([]);
    const rowCount = ref(10);
    const loading = ref(false);

    //rows.value.reverse();

    return {
      setup: setup,
      newregister: newregister,
      user: user,
      color: "cyan", 
      leftDrawerOpen: ref(false),
      rows: rows,
      item: ref(item),
      rowCount,
      columns,
      loading,
      apiLoad:'default',

    };
  },
  watch: {
    today(){
      this.getServicos()
    }
    
  },
  methods: {
      async addRow() {
        
        let d = new Date();
        let h = d.getHours();
        const index = 0;

        const row = this.item[0];
        row.id = index;

        row.hour = h;

        this.updateApi(null, 'hour', h, null)

        //newregister = row;

        const newRow = { ...row }; // extend({}, row, { name: `${row.name} (${row.__count})` })

        

        this.rows = [
          ...this.rows.slice(0, index),
          newRow,
          ...this.rows.slice(index),
        ];

        console.log('item:',this.rows[0])        

        this.rows.value.reverse();


      },
    async getServicos() {
      
      if(this.card.id && this.today){
        
        const req = await fetch(
          this.setup.url +"/"+ this.card.id +"/"+ this.today,{
            headers: {
              'Cache-Control': 'no-cache'
            }
          }

        );
        //const req = await fetch(this.setup.url);
        
        this.data = await req.json();

        this.apiLoad = "get start ok: " + this.data.data.card

        if (this.data.status === "error") {
          this.setup.noResultsLabel = this.data.data;
          this.setup.noDataLabel = this.data.data;
        } else {
          this.rows = this.data.data.card;
        }

      } else {
         if (this.data.status === "error") {
          setTimeout(function(){ this.getServicos() },10000)
         }
      }
    },
    async updateApi(id, key, value, scope = null) {
      this.loading = true

      console.log('scope')

      const array = [];
      array['id'] = id;
      array['caid'] = parseInt(this.card.id);
      array['pid'] = parseInt(this.card.pid);
      array['key'] = key;
      array[key] = value;    
      array['status'] = 'active';   

      const dataJson = JSON.stringify(Object.assign({}, array));        

      const req = await fetch(this.setup.url, {
          method: "POST",
          mode: 'cors',
          headers: {
              'Origin': this.setup.origin,
              'Accept': 'application/json, text/javascript, */*; q=0.01',
              'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          }, 
          body: dataJson,
      });
      const res = await req.json();
      console.log(res);
      if(scope){
        scope.set();
      }
      
      if(key === 'name'){
          //this.getServicos();
          
      }
      this.loading = false
    },


  },
  async mounted() {   
    this.getServicos()
  },
  updated() {    
    this.apiLoad = true
  },
  async created(){
    
  },


};
</script>

<style lang="sass">

.my-sticky-header-column-table
  /* height or max-height is important */
  height: 50vh

  /* specifying max-width so the example can
    highlight the sticky column on any browser window */
  max-width: 100%

  td:first-child
    /* bg color is important for td; just specify one */
    background-color: #009999 !important
    color: #ffffff !important

  tr th
    position: sticky
    /* higher than z-index for td below */
    z-index: 2
    /* bg color is important; just specify one */
    background: #fff

  /* this will be the loading indicator */
  thead tr:last-child th
    /* height of all previous header rows */
    top: 48px
    /* highest z-index */
    z-index: 3
  thead tr:first-child th
    top: 0
    z-index: 1
  tr:first-child th:first-child
    /* highest z-index */
    z-index: 3

  td:first-child
    z-index: 1

  td:first-child, th:first-child
    position: sticky
    left: 0

</style>