<script>
import { ref } from 'vue'
const stringOptions = [
  'Ultrassom (Terceiriz.)'
]
export default{
    name: 'TabImagens',
    props: {
        card: Array,
    },
    setup() {
        const filterOptions = ref(stringOptions)
        return{
            title: "Imagens",
            color: "brown",            
            model: ref(null),
            filterOptions,
            createValue (val, done) {
                if (val.length > 0) {
                    if (!stringOptions.includes(val)) {
                        stringOptions.push(val)
                    }
                    done(val, 'toggle')
                }
            },
        }
    },
    methods:{
