<template>
    <div>
        <TabMedicamentos :card="card"/>
        <TabAmbulatorio :card="card"/>
        <TabAlimentacao :card="card"/>
        <TabProcedimentos :card="card"/>
        <TabTransfusoes :card="card"/>

    </div>
</template>
<style lang="sass">

</style>
<script>
import { ref } from 'vue'

import TabMedicamentos from "../../components/Pacientes/TabMedicamentos.vue";
import TabProcedimentos from "../../components/Pacientes/TabProcedimentos.vue";
import TabAlimentacao from "../../components/Pacientes/TabAlimentacao.vue";
import TabAmbulatorio from "../../components/Pacientes/TabAmbulatorio.vue";
import TabTransfusoes from "../../components/Pacientes/TabTransfusoes.vue";


const stringOptions = [
  'Cerenia 1 mL', 'Imizol', 'Metadona', 'Hidrocortisona', 'Manitol'
]
export default{
    name: 'TabProtocolos',
    components: {
        TabMedicamentos,
        TabProcedimentos,
        TabAlimentacao,
        TabAmbulatorio,
        TabTransfusoes
    },
    props: {
        card: Array,
    },
    setup() {
        const filterOptions = ref(stringOptions)
        return{
            //ativo: false
            color: "cyan",            
            model: ref(null),
            filterOptions,
            createValue (val, done) {
                if (val.length > 0) {
                    if (!stringOptions.includes(val)) {
                        stringOptions.push(val)
                    }
                    done(val, 'toggle')
                }
            },
        }
    },
    methods:{
        ativar(){
            //this.ativo = !this.ativo
        }

    }
}
    
</script>