<template>
    <div>
        
        <p :class="'q-pt-lg text-h6 text-'+color">{{title}}</p>
        <p :class="'text-'+color">{{description}} </p>
        <form id="boletim-form" @submit.prevent="setBulletin">
        <div class="row q-col-gutter-sm q-col-gutter-md  q-pt-lg q-pb-lg ">
            {{bulletinDescription}}
                <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                    <q-input
                        v-model="bulletinDescription"
                        filled
                        clearable
                        autogrow
                        :color="color"
                        label="Digite aqui"
                    />
                </div>
                <div class="col-xs-12 col-sm-12 col-md-5 col-lg-5">
                    <q-input filled :color="color" v-model="date">
                        <template v-slot:prepend>
                            <q-icon name="event" class="cursor-pointer">
                            <q-popup-proxy cover :color="color" transition-show="scale" transition-hide="scale">
                                <q-date v-model="date" mask="YYYY-MM-DD HH:mm">
                                <div class="row items-center justify-end">
                                    <q-btn v-close-popup label="Close" color="primary" flat />
                                </div>
                                </q-date>
                            </q-popup-proxy>
                            </q-icon>
                        </template>

                        <template v-slot:append>
                            <q-icon name="access_time" class="cursor-pointer">
                            <q-popup-proxy cover transition-show="scale" transition-hide="scale">
                                <q-time v-model="date" mask="YYYY-MM-DD HH:mm" format24h>
                                <div class="row items-center justify-end">
                                    <q-btn v-close-popup label="Close" color="primary" flat />
                                </div>
                                </q-time>
                            </q-popup-proxy>
                            </q-icon>
                        </template>
                    </q-input>
                </div>
                <div class="col-xs-12 col-sm-12 col-md-5 col-lg-5">
                    <q-file id="fileBoletim" name="fileBoletim" filled bottom-slots v-model="file" label="Arquivo" counter>
                        <template v-slot:prepend>
                        <q-icon name="cloud_upload" @click.stop />
                        </template>
                        <template v-slot:append>
                        <q-icon name="close" @click.stop="model = null" class="cursor-pointer" />
                        </template>

                        <template v-slot:hint>
                        Anexar arquivo
                        </template>
                    </q-file>
                </div>

                <div class="col-xs-12 col-sm-12 col-md-2 col-lg-2">
                    <q-btn type="submit" :color="color" label="Salvar" class="full-width" padding="16px 5px"/> 
                </div>
                    
        </div>
        </form> 
    <template>
        <div>
            {{today}}
            {{role}}
            {{load}}
        </div>
    </template>

    <q-btn-toggle
        v-model="role"
        :toggle-color="color"
        no-caps
        unelevated
        color="grey-1"
        text-color="grey"
        class="q-mt-md q-mb-md"
        :options="[
            {label: 'Todos', value: ''},
            {label: 'Mdico', value: 'vet'},
            {label: 'Cirurgio', value: 'surgeon'},
            {label: 'Especialista', value: 'expert'},
            {label: 'Enfermagem', value: 'nursing'}
        ]"
    />

        <q-timeline v-if="bulletin" layout="dense" color="secondary">
            <q-timeline-entry
                
                v-for="bu in bulletin"                
                :key="bu.id" 
                :title="bu.name"
                :color="color"
                :text-color="color"
                :subtitle="bu.data_format"
                :avatar="bu.avatar"
                :style="(bu.role !== role && role !== '') ? 'display:none' :''"
                body=''
            ><q-badge :color="color" text-color="white" :label="bu.roleLabel" /><br/>{{bu.description}}<br/>

            <br/>
            
                <q-expansion-item
                v-if="bu.file"
                group="filegroup"
                icon="las la-file-alt"
                label="Arquivo"
                >
                <q-card>
                    <q-card-section>
                        <iframe :src="bu.file" width="100%" height="500px"></iframe>
                        <!-- <embed :src="bu.file" width="100%" height="500" alt="pdf" loading="lazy" pluginspage="http://www.adobe.com/products/acrobat/readstep2.html"/>-->
                    </q-card-section>
                </q-card>
                </q-expansion-item>
            </q-timeline-entry>      
        </q-timeline>
    </div>
</template>
<style lang="sass">

</style>
<script>
import { ref } from 'vue'

const date = new Date();

const setup = {
    title: "Boletins & Anotaes",
    description: "Mauris aliquet dignissim odio, sed condimentum erat.",
    origin: process.env.VUE_APP_URL_API_ORIGIN,
    //url: 'https://clientes.test/invet/api/bulletin',
    url: process.env.VUE_APP_URL_API_BULLETIM,//'https://clientes.test/invet/api/bulletin',
    day: String(date.getDate()).padStart(2,'0'),
    month: String(date.getMonth()+1).padStart(2,'0'),
    year: date.getFullYear()
   
};

const stringOptions = [
  'Ultrassom (Terceiriz.)'
]
export default{
    name: 'TabBoletim',
    props: {
        id: String,
        today: String
    },
    setup() {
        const filterOptions = ref(stringOptions)
        return{
            setup: setup,
            expanded: ref(false),
            user: JSON.parse(localStorage.getItem('user')),
            title: setup.title,
            description: setup.description,
            bulletinDescription:'',
            bulletin: null,
            data: null,
            date: ref(setup.year+'-'+setup.month+'-'+setup.day),
            role: ref('vet'),
            //date: setup.year+'-'+setup.month+'-'+'26',//setup.day
            color: "teal",            
            model: ref(null),
            file: ref(null),
            filterOptions,
            load: "default",
            createValue (val, done) {
                if (val.length > 0) {
                    if (!stringOptions.includes(val)) {
                        stringOptions.push(val)
                    }
                    done(val, 'toggle')
                }
            },
        }
    },
    watch: {

        today(){
            this.data = null
            this.getBulletin()
        },
        load(){
            this.getBulletin()
        } 
        

    },
    methods:{
        getBase64(file) {
            return new Promise((resolve, reject) => {
                const reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = () => resolve(reader.result);
                reader.onerror = error => reject(error);
            });
        },
        loadBulletin() {

            if(this.data === null){
 
               return setTimeout(function(){ this.getBulletin() },10000)
            }

            if(this.data.status === 'error'){ 
                    this.data = null
                    this.bulletin = null
                    return setTimeout(function(){ this.getBulletin() },10000)
                } else {            
                

                    let options = { year: 'numeric', month: 'long', day: 'numeric' };
                    this.data.data.bulletin.map(function(nu){
                        switch (nu.role){

                            case 'vet': nu.roleLabel = 'Boletim Mdico'
                            break;
                            case 'assistant': nu.roleLabel = 'Assistente'
                            break;
                            case 'technical': nu.roleLabel = 'Tcnico'
                            break;
                            case 'expert': nu.roleLabel = 'Especialista'
                            break;
                            case 'nursing': nu.roleLabel = 'Anotaes de enfermagem'
                            break;
                            case 'surgeon': nu.roleLabel = 'Cirurgio'
                            break;

        

                        }
                        nu.data_format = new Date(nu.created_at).toLocaleTimeString() + ' - ' + new Date(nu.created_at).toLocaleDateString("pt-BR", options)
                        nu.expanded = false
                        return nu.data_format
                    });

                      
    
                      console.log('loadBulletin/: bulletin',this.data.data.bulletin)
                      

                      this.bulletin = this.data.data.bulletin

                      return this.bulletin


                    //return          


                }

        },
        async getBulletin() {
            this.load = ref(this.today)
            this.data = null
            this.bulletin = null    
            //if(this.selectDate){
                //const req = await fetch(this.setup.url +'/'+id+'/'+isdate );
                //this.data = await req.json();

                this.data = await fetch(this.setup.url +'/'+this.id+'/'+this.today,
                {
                    headers: {
                    'Cache-Control': 'no-cache'
                    }
                } 
                
                ).then((req) => req.json())
                this.load = this.setup.url +'/'+this.id+'/'+this.today
                this.loadBulletin()

                //this.data = await fetch(this.setup.url +'/1/2022-07-26' ).then((req) => req.json()) 

            //} else {
                
               // setTimeout(function(){ this.getBulletin() },10000)
           /// }            
        },
        async setBulletin() {
            console.log(this.id,this.user.id,this.date,this.bulletinDescription, this.file)
            console.log(this.file)

            console.log('File Base 64', await this.getBase64(this.file))

            const data = {
                transaction: 'insert',
                caid: Number(this.id),
                coid: Number(this.user.id),
                description: this.bulletinDescription,
                filebase64: await this.getBase64(this.file),
                status: 'active'
            };


            const dataJson = JSON.stringify(data);
            //const dataJson = data;
        

            if (this.bulletinDescription) {
                const req = await fetch(this.setup.url, {
                    method: "POST",
                    mode: 'cors',
                    headers: {
                        'Origin': this.setup.origin,
                        'Accept': 'application/json, text/javascript, */*; q=0.01',
                        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
                    }, 
                    body: dataJson,
                });

                const res = await req.json();
                console.log(res);
            }
        }
    },
    beforeCreate() {       
        console.log("beforeCreate: ", this.data)
    },
    created() {
        this.getBulletin()     
        console.log("created: ", this.data)
    },
    beforeMount() {
        console.log("beforeMount: ", this.data)        
    },
    mounted() {     
        this.load = "load"   
        
        
        console.log("mounted: ", this.data)
    },
    //Atualizao
    beforeUpdate(){       
        
        console.log("beforeUpdate: ", this.bulletin)
        
    },
    update(){
        
        console.log("update: ", this.data)        
    },
    beforeUnmount(){
        
        console.log("beforeUnmount: ", this.data)        
    },
    unmounted(){
        
        console.log("unmounted: ", this.data)
        
    },
    computed:{

    },

}
    
</script>