<template>
  <div>

        <div :class="'q-pt-xl text-h6 text-'+color"><q-icon name="las la-band-aid" /> {{title}}</div>
        <p :class="'text-'+color">{{desc}}</p>

    <!--Table-->
    
    
      <!--Table-->
      <q-table
        class="max-width:100% no-shadow"
        :selected-rows-label="labelDeletar"
        :rows="rows"
        :columns="columns"
        color="grey-1"
        :rows-per-page-options="[1000]"
        row-key="id"
        no-data-label="Desculpe. Algo inesperado ocorreu!"
        no-results-label="Nenhum resultado foi encontrado!"
        hide-pagination
        selection="single"
        v-model:selected="selected"
      >
            <template v-slot:top-right>
        <q-btn
          v-if="selected != ''"
          class="q-mr-sm"
          color="red text-white"
          icon="las la-trash-alt"
          label="Deletar"
          @click="deleteApi(selected)"
        />
        <q-btn
          outline          
          color="grey"
          icon="las la-plus"
          label=""
          @click="addRow"
        />
      </template>


        <template v-slot:body="props">
          <q-tr :id="props.row.id" v-if="props.row.status" :props="props" :key="props.row.id">
            <!--td medicamentos-->
            <q-td><q-checkbox dense v-model="props.selected"/></q-td>
            <q-td key="medicamentos" :props="props">
              {{ props.row.medicamentos }}
              <q-popup-edit
                class="bg-grey-2"
                v-model="props.row.medicamentos"
                v-slot="scope"
                label-set="Salvar"
                label-cancel="Cancelar"
              >
                <q-input
                  v-model="scope.value"
                  dense
                  autofocus
                  counter
                  @keyup.enter="
                    updateApi(props.row.id, 'medicamentos', scope.value, scope)
                  "
                >
                  <template v-slot:append>
                    <q-icon name="las la-pen" />
                  </template>
                  <template v-slot:after>
                    <q-btn
                      flat
                      dense
                      color="negative"
                      icon="cancel"
                      @click.stop="scope.cancel"
                    />

                    <q-btn
                      flat
                      dense
                      color="positive"
                      icon="check_circle"
                      @click.stop="
                        updateApi(props.row.id, 'medicamentos', scope.value, scope)
                      "
                      :disable="
                        scope.validate(scope.value) === false ||
                        scope.initialValue === scope.value
                      "
                    />
                  </template>
                </q-input>
              </q-popup-edit>
            </q-td>
            <!--end td medicamentos-->
            <!--td vol-->
            <q-td key="vol" :props="props">
              {{ props.row.vol }}
              <q-popup-edit
                class="bg-grey-2"
                v-model.number="props.row.vol"
                v-slot="scope"
                label-set="Salvar"
                label-cancel="Cancelar"
              >
                <q-input
                  type="number"
                  v-model.number="scope.value"
                  dense
                  autofocus
                  @keyup.enter="
                    updateApi(props.row.id, 'vol', scope.value, scope)
                  "
                >
                  <template v-slot:append>
                    <q-icon name="las la-pen" />
                  </template>
                  <template v-slot:after>
                    <q-btn
                      flat
                      dense
                      color="negative"
                      icon="cancel"
                      @click.stop="scope.cancel"
                    />

                    <q-btn
                      flat
                      dense
                      color="positive"
                      icon="check_circle"
                      @click.stop="
                        updateApi(props.row.id, 'vol', scope.value, scope)
                      "
                      :disable="
                        scope.validate(scope.value) === false ||
                        scope.initialValue === scope.value
                      "
                    />
                  </template>
                </q-input>
              </q-popup-edit>
            </q-td>
            <!--end td vol-->
            <!--td via-->
            <q-td key="via" :props="props">
              {{ props.row.via }}
              <q-popup-edit
                class="bg-grey-2"
                v-model="props.row.via"
                v-slot="scope"
                label-set="Salvar"
                label-cancel="Cancelar"
              >

                <q-select
                  type="text"
                  :options="['Oral','Venosa']"
                  v-model="scope.value"
                  dense
                  autofocus
                  @keyup.enter="
                    updateApi(props.row.id, 'via', scope.value, scope)
                  "
                >
                  <template v-slot:append>
                    <q-icon name="las la-pen" />
                  </template>
                  <template v-slot:after>
                    <q-btn
                      flat
                      dense
                      color="negative"
                      icon="cancel"
                      @click.stop="scope.cancel"
                    />

                    <q-btn
                      flat
                      dense
                      color="positive"
                      icon="check_circle"
                      @click.stop="
                        updateApi(props.row.id, 'via', scope.value, scope)
                      "
                      :disable="
                        scope.validate(scope.value) === false ||
                        scope.initialValue === scope.value
                      "
                    />
                  </template>
                </q-select>
              </q-popup-edit>
            </q-td>
            <!--end td via-->
            <!--td freq-->
            <q-td key="freq" :props="props">
              {{ props.row.freq }}
              <q-popup-edit
                class="bg-grey-2"
                v-model.number="props.row.freq"
                v-slot="scope"
                label-set="Salvar"
                label-cancel="Cancelar"
              >
                <q-select
                  type="text"
                  :options="['1x','2x','3x','4x','6x', '8x', '12x', '24x']"
                  v-model="scope.value"
                  dense
                  autofocus
                  @keyup.enter="
                    updateApi(props.row.id, 'freq', scope.value, scope)
                  "
                >
                  <template v-slot:append>
                    <q-icon name="las la-pen" />
                  </template>
                  <template v-slot:after>
                    <q-btn
                      flat
                      dense
                      color="negative"
                      icon="cancel"
                      @click.stop="scope.cancel"
                    />

                    <q-btn
                      flat
                      dense
                      color="positive"
                      icon="check_circle"
                      @click.stop="
                        updateApi(props.row.id, 'freq', scope.value, scope)
                      "
                      :disable="
                        scope.validate(scope.value) === false ||
                        scope.initialValue === scope.value
                      "
                    />
                  </template>
                </q-select>
              </q-popup-edit>
            </q-td>
            <!--end td freq-->
            <!--td Note-->
            <q-td key="note" :props="props">
              {{ props.row.note }}
              <q-popup-edit
                class="bg-grey-2"
                v-model.number="props.row.note"
                v-slot="scope"
                label-set="Salvar"
                label-cancel="Cancelar"
              >
                <q-input
                  type="number"
                  v-model.number="scope.value"
                  dense
                  autofocus
                  @keyup.enter="
                    updateApi(props.row.id, 'note', scope.value, scope)
                  "
                >
                  <template v-slot:append>
                    <q-icon name="las la-pen" />
                  </template>
                  <template v-slot:after>
                    <q-btn
                      flat
                      dense
                      color="negative"
                      icon="cancel"
                      @click.stop="scope.cancel"
                    />

                    <q-btn
                      flat
                      dense
                      color="positive"
                      icon="check_circle"
                      @click.stop="
                        updateApi(props.row.id, 'note', scope.value, scope)
                      "
                      :disable="
                        scope.validate(scope.value) === false ||
                        scope.initialValue === scope.value
                      "
                    />
                  </template>
                </q-input>
              </q-popup-edit>
            </q-td>
            <!--end td note-->
            
            
            
          </q-tr>
        </template>
      </q-table>
      <!--Table end-->
    
    
  </div>
</template>

<style></style>

<script>
import { ref } from "vue";

const path = "protocolosmedicamentos";
const setup = {
  url: `https://wrochadesign.com.br/clientes/invet/?t=${path}`,
  urlId: `https://wrochadesign.com.br/clientes/invet/?t=${path}&id=`,
};

const columns = [
  //{ name: "id", label: "id", field: "id" },
  {
    name: "medicamentos",
    label: "Procedimentos",
    field: "medicamentos",
    sortable: true,
    required: true, 
    align: 'left'
    
  },
  { name: "vol", label: "vol. Ml ", field: "vol", align: 'left' },
  { name: "via", label: "Via", field: "via", align: 'left' },
  { name: "freq", label: "Taxa", field: "freq", align: 'left' },
  { name: "note", label: "Observao", field: "note", align: 'left' }


  
];

const item = [
  {
    id: 0,
    //idpet: 1,
    medicamentos: "",
    vol: "0",
    via: "0",
    freq: "0",
    h06: "0",
    h07: "0",
    h08: "0",
    h09: "0",
    h10: "0",
    h11: "0",
    h12: "0",
    h13: "0",
    h14: "0",
    h15: "0",
    h16: "0",
    h17: "0",
    h18: "0",
    h19: "0",
    h20: "0",
    h21: "0",
    h22: "0",
    h23: "0",
    h24: "0",
    h01: "0",
    h02: "0",
    h03: "0",
    h04: "0",
    h05: "0",
    status: 1,
    dataTime: ""
  },
];

export default {
  name: "TabMedicamentos",
  components: {},
  props: {
    card: Array,
  },
  setup() {
    const rows = ref([]);
    const rowCount = ref(10);
    const loading = ref(false);

    rows.value.reverse();

    return {
      color: "purple",
      setup: setup,
      selected: ref([]),
      filter: ref(""),
      leftDrawerOpen: ref(false),
      rows,
      item: ref(item),
      rowCount,
      columns,
      loading,
      async addRow() {
        const index = rows.value.length + 1;

        const row = item[0];
        row.id = index;

        const newRow = { ...row }; // extend({}, row, { name: `${row.name} (${row.__count})` })

        rows.value = [
          ...rows.value.slice(0, index),
          newRow,
          ...rows.value.slice(index),
        ];

        rows.value.reverse();

        const id = newRow.id;
        console.log(id);

        const dataJson = JSON.stringify(newRow);

        const req = await fetch(this.setup.url, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: dataJson,
        });

        const res = await req.json();
        console.log(res);
      },
    };
  },
  data() {
    return {
      title: "Ambulatrio",
      desc: "Lorem ipsum dolor sit amet consectetur adipisicing elit.", 
    };
  },
  methods: {
    labelDeletar() {
      return "Clique no boto deletar para excluir esse item.";
    },
    async getServicos() {
      const req = await fetch(this.setup.url);
      this.rows = await req.json();
      console.log(this.rows);
    },
    async setApi(dataJson) {
      const req = await fetch(this.setup.url, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: dataJson,
      });

      const res = await req.json();
      console.log(res);
    },

    async updateApi(id, key, value, scope) {
      const array = [];
      array[key] = value;
      console.log(array);

      const dataJson = JSON.stringify(Object.assign({}, array));

      const req = await fetch(this.setup.urlId + id, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: dataJson,
      });
      const res = await req.json();
      console.log(res);
      scope.set();
    },
    async deleteApi(array) {
      const id = array[0].id;
      console.log(id);

      const dataJson = JSON.stringify({ status: 0 });

      const req = await fetch(this.setup.urlId + id, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: dataJson,
      });

      const res = await req.json();
      this.selected = "";
      console.log(res);
      this.getServicos();
    },
  },
  mounted() {
    this.getServicos();
  },
};
    
</script>